#firstpage-container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;height:100%;min-height:100vh;width:100vw;padding:25px}#firstpage-container h1{font-family:"Proza Libre";font-size:1.5rem;font-weight:500;color:black;margin-bottom:10px}.input-wrapper{width:100%;margin-bottom:20px}.input-wrapper p{font-size:.8rem;font-weight:700}.input-wrapper input{margin-top:10px;width:100%;height:36px;border:1px solid #acabab;border-radius:12px;text-align:center}

.custombutton-container{background-color:#cfb696;padding:0 50px;line-height:60px;height:60px;font-size:1.5rem;font-family:"Proza Libre";font-weight:500;border:none;border-radius:30px;margin-top:30px;cursor:pointer}

#secondpage-container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;height:100%;min-height:100vh;width:100%;position:relative}#secondpage-container h1{font-family:"Proza Libre";font-size:3rem;font-weight:500;margin-top:20px;color:black}#secondpage-container h2{font-family:"Proza Libre";font-size:1.5rem;font-weight:500}#age-picker{width:100%;height:30%;position:absolute;bottom:0;overflow:scroll}#age-picker-overlay{position:absolute;z-index:1;bottom:0;left:0;pointer-events:none;background-image:linear-gradient(rgba(255,255,255,0), #fff 70%);width:100%;height:200px}.age-choice{width:100%;height:40px;line-height:40px;font-family:"Proza Libre";font-size:1.5rem;text-align:center;cursor:pointer}.selected-age{background-color:#cfb696}

#thirdpage-container{width:100%;height:100%;min-height:100vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center;padding:25px}#thirdpage-container h1{font-family:"Proza Libre";font-size:1.5rem}#age-range-picker{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:center;justify-content:center;grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px;grid-row-gap:15px;row-gap:15px}.age-range{width:100px;height:100px;text-align:center;line-height:100px;background-color:rgba(207,182,150,0.4);border-radius:50%;font-size:1.5rem;border:2px solid transparent}.selected-range{border:2px solid black}

#fourth-page-container{width:100%;height:100%;min-height:100vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center;padding:25px}#fourth-page-container h1{font-family:"Proza Libre";font-size:1.5rem}#offer-picker{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:center;justify-content:center;grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px;grid-row-gap:15px;row-gap:15px}.offer{width:100px;height:100px;text-align:center;line-height:100px;background-color:rgba(207,182,150,0.4);border-radius:50%;font-size:1.5rem;border:2px solid transparent;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;cursor:pointer}.offer p{width:100px;height:1rem;line-height:1rem;font-size:.8rem;margin-bottom:12px;pointer-events:none}.offer img{pointer-events:none}.selected-offer{border:2px solid black}

#fifthpage-container{width:100%;height:100%;min-height:100vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center;padding:25px}#fifthpage-container h1{font-family:"Proza Libre";font-size:1.5rem}#self-description-picker{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:center;justify-content:center;grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px;grid-row-gap:15px;row-gap:15px}.self-description{width:100px;height:100px;text-align:center;line-height:100px;background-color:rgba(207,182,150,0.4);border-radius:50%;font-size:1.5rem;border:2px solid transparent;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;cursor:pointer}.self-description p{width:100px;height:1rem;line-height:1rem;font-size:.8rem;margin-bottom:12px;pointer-events:none}.self-description img{pointer-events:none}.selected-self-description{border:2px solid black}

#matchselection-container{width:100vw;height:100vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;padding:25px;font-family:"Proza Libre"}#matchselection-container h1{font-size:1.5rem;margin-bottom:50px}#matchselection-container p{font-size:1rem;background-color:rgba(207,182,150,0.4);background:linear-gradient(to right, white, rgba(207,182,150,0.4) 15% 85%, white);width:100%;height:40px;line-height:40px;text-align:center;margin-bottom:30px}.matchinfo-wrapper{width:100%;height:140px;margin-bottom:30px;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center}.match-name{height:100px;width:100px;border-radius:50%;text-align:center;line-height:100px;background-color:rgba(207,182,150,0.4)}.match-info{height:100%;width:70%;border-radius:10px;padding:10px;background-color:rgba(207,182,150,0.4);display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}

#pagemarker-container{width:100%;max-width:400px;height:50px;margin:0 auto;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;grid-column-gap:6px;-webkit-column-gap:6px;column-gap:6px;-webkit-align-content:center;align-content:center}#pagemarker-wrapper{width:100%;height:50px;position:absolute}

.marker{width:40px;height:3px;background-color:#acabab;margin-top:30px}.marker-active{background-color:#94744b}

#form-container{height:100vh;width:100vw;position:relative;margin:0 auto}

#login-container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;grid-row-gap:20px;row-gap:20px;height:100%;min-height:100vh;width:100vw;padding:25px}#login-container h1{font-family:"Proza Libre";font-size:1.5rem;font-weight:500;color:black;margin-bottom:10px;margin-top:60px}.form-container{width:100%;margin-bottom:20px}.form-container label{font-size:.8rem;font-weight:700}.form-container input{margin-top:10px;margin-bottom:30px;width:100%;height:36px;border:1px solid #acabab;border-radius:12px;text-align:center}

.container{background-position:center;background-repeat:no-repeat;background-size:cover;height:100vh;width:100vw;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;justify-self:center;-webkit-justify-content:center;justify-content:center;-webkit-align-self:center;align-self:center}.fuzz{background-color:#cfb69633;width:100%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.btn{background:#e8d2b1;border:1px solid #e5e5e5;box-sizing:border-box;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;border-radius:100px;width:300px;font-family:Roboto;font-style:normal;font-weight:normal;font-size:36px;line-height:42px;padding:10px;margin:20px;flex-direction:column;-webkit-align-items:center;align-items:center;text-decoration:none}.btn-container{margin-top:4vmin}.homepage-title{color:#fbf6f0;font-family:"Proza Libre";font-size:3rem;line-height:5rem}.stupid-inspirational-quote{color:#fbf6f0;font-family:"Proza Libre";font-size:1.5rem;padding-bottom:2vh}.link{text-decoration:none}

#onboarding-container{width:100%;min-width:100vw;height:100%;min-height:100vh}

#onpage1-container{width:100%;height:100vh;background-color:rgba(207,182,150,0.4);display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;font-family:"Proza Libre";padding:25px}#onpage1-container h1{font-size:3rem;color:#f0f0f0;text-shadow:1px 3px 5px rgba(0,0,0,0.981)}#onpage1-container h2{font-size:1.8rem;font-weight:700;text-align:center}#onpage1-container h3{font-size:1.5rem;font-weight:300;text-align:center;margin-top:10px}#onpage1-container img{width:300px;height:300px}

#onpage2-container{width:100%;height:100vh;background-color:rgba(207,182,150,0.4);display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;font-family:"Proza Libre";padding:25px}#onpage2-container h1{font-size:3rem;color:#f0f0f0;text-shadow:1px 3px 5px rgba(0,0,0,0.398)}#onpage2-container h2{font-size:1.8rem;font-weight:700}#onpage2-container h3{font-size:1.5rem;font-weight:300;text-align:center;margin-top:10px}#onpage2-container img{width:300px;height:300px}

#onpage3-container{width:100%;height:100vh;background-color:rgba(207,182,150,0.4);display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;font-family:"Proza Libre";padding:25px}#onpage3-container div{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}#onpage3-container #flipped{-webkit-transform:scaleX(-1);transform:scaleX(-1)}#onpage3-container h2{font-size:1.8rem;font-weight:700}#onpage3-container h3{font-size:1.5rem;font-weight:300;text-align:center;margin-top:10px}#onpage3-container img{width:170px;height:300px}

#messagepage-container{width:100vw;height:100%;min-height:100vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;font-family:"Proza Libre";padding:25px}#messagepage-container h1{width:100%;height:1.5rem;font-size:1.5rem;font-weight:700;margin-bottom:30px}#messagepage-container label{width:100%;height:1rem;font-size:1rem;font-weight:500;margin-bottom:10px}#messagepage-container textarea{border-radius:10px;margin-bottom:60px;padding:10px}


